import styled from "styled-components"
import {Link as LinkR} from "react-router-dom"
import {Link as LinkS} from "react-scroll"

export const Nav = styled.nav`
    background:${({scrollNav})=>(scrollNav ? "blueviolet" : "transparent")};
    height:80px;
    margin-top:-80px;
    display:flex;
    justify-content:center;
    align-items:center;
    font-size:1rem;
    position:sticky;
    top:0;
    z-index:10;

    @media screen and (max-width:960px){
        transition: 0.8s all ease;
    }
`
export const NavbarContainer = styled.div`
    display:flex;
    justify-content:space-between;
    height: 80px;
    z-index: 1;
    width:100%;
    max-width: 1100px;
`
export const NavLogo = styled(LinkR)`
    color:#fff;
    cursor:pointer;
    font-size:40px;
    align-items:center;
    font-weight:bold;
    text-decoration:none;
    margin-top:15px;
    float:left;
    padding:5px;

    &:hover{
        transition: all 0.2s ease-in-out;
        color:white;
        box-shadow: -10px -5px white;
        background:rgb(140, 69, 207); 
    }
`;
export const MobileIcon = styled.div`
    display:none;
    
    @media screen and (max-width: 760px){
        display:block;
        position:absolute;
        top:0;
        right:0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor:pointer;
        color:#fff;
    }
`;
export const NavMenu = styled.ul`
    display:flex;
    align-items:center;
    list-style:none;
    text-align:center;
    margin-right: -22px;

    @media screen and (max-width: 768px){
        display:none;
    }
`;
export const NavItem = styled.li`
    height:80px;
`;
export const NavLinks = styled(LinkS)`
    color: white;
    display:flex;
    align-items:center;
    text-decoration:none;
    padding: 0 1rem;
    height: 100%;
    cursor:pointer;
    font-size:25px;

    &:hover{
        background:rgb(140, 69, 207);
        color:black;
    }

    &.active{
        border-bottom: 3px solid black;
    }
`;
export const NavBtn = styled.nav`
    display:flex;
    align-items:center;

    @media screen and (max-width:768px){
        display:none;
    }
`;
export const NavBtnLink = styled(LinkR)`
    border-radius:2px 10px;
    background: white;
    white-space:nowrap;
    padding:5px 5px;
    color:blueviolet;
    font-size:25px;
    outline:none;
    border:none;
    cursor:pointer;
    transition:all 0.2s ease-in-out;
    text-decoration:none;
    

    &:hover{
        transition: all 0.2s ease-in-out;
        background:rgb(140, 69, 207);
        color:white;
        box-shadow: 5px 10px white;
    }
`;